___CSS_LOADER_EXPORT___.push([module.id, `.btn {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.2s ease-in-out;
  position: relative;
  overflow: hidden;
}

/* Sizes */
.btn-small {
  height: 32px;
  padding: 0 16px;
}

.btn-medium {
  height: 36px;
  padding: 0 24px;
}

.btn-large {
  height: 48px;
  padding: 0 24px;
}

/* Colors & Variants */
.btn-filled.btn-primary {
  background-color: #0095e0;
  color: #ffffff;
}

.btn-filled.btn-primary:hover:not(:disabled) {
  background-color: #007ab8;
}

.btn-filled.btn-secondary {
  background-color: #ffffff;
  color: #0095e0;
  border: 1px solid #0095e0;
}

.btn-filled.btn-secondary:hover:not(:disabled) {
  background-color: #f5f5f5;
}

.btn-filled.btn-danger {
  background-color: #d81a26;
  color: #ffffff;
}

.btn-filled.btn-danger:hover:not(:disabled) {
  background-color: #b0151f;
}

.btn-outlined {
  background-color: transparent;
  border: 1px solid currentColor;
}

.btn-outlined.btn-primary {
  color: #0095e0;
}

.btn-outlined.btn-primary:hover:not(:disabled) {
  background-color: rgba(0, 149, 224, 0.04);
}

.btn-outlined.btn-secondary {
  color: #666666;
}

.btn-outlined.btn-secondary:hover:not(:disabled) {
  background-color: rgba(102, 102, 102, 0.04);
}

.btn-outlined.btn-danger {
  color: #d81a26;
}

.btn-outlined.btn-danger:hover:not(:disabled) {
  background-color: rgba(216, 26, 38, 0.04);
}

.btn-text {
  background-color: transparent;
  padding: 0 8px;
}

.btn-text.btn-primary {
  color: #0095e0;
}

.btn-text.btn-primary:hover:not(:disabled) {
  background-color: rgba(0, 149, 224, 0.04);
}

.btn-text.btn-secondary {
  color: #666666;
}

.btn-text.btn-secondary:hover:not(:disabled) {
  background-color: rgba(102, 102, 102, 0.04);
}

.btn-text.btn-danger {
  color: #d81a26;
}

.btn-text.btn-danger:hover:not(:disabled) {
  background-color: rgba(216, 26, 38, 0.04);
}

/* Disabled state */
.btn:disabled,
.btn-disabled {
  background-color: #bebec1 !important;
  color: #ffffff !important;
  border-color: transparent !important;
  cursor: not-allowed;
}

/* Icons */
.btn-icon {
  font-size: 18px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

/* Loading state */
.btn-loading {
  color: transparent !important;
}

.btn-spinner {
  position: absolute;
  width: 18px;
  height: 18px;
  border: 2px solid currentColor;
  border-radius: 50%;
  border-right-color: transparent;
  animation: spin 0.75s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
